import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { auth } from '@/lib/firebase';
import { signInWithPhoneNumber } from 'firebase/auth';

// Async thunk for registering user
export const registerUser = createAsyncThunk(
  'auth/registerUser',
  async ({ name, mobile, age, gender, region, apiBase }, { rejectWithValue }) => {
    try {
      const response = await fetch(`${apiBase}/api/bookingconsultancy/register`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          name,
          mobile,
          age,
          gender,
          region,
          healthIssue: 'hair problem',
        }),
      });

      const data = await response.json();
      if (!data.success) {
        return rejectWithValue(data.message || 'Registration failed');
      }
      return data;
    } catch (error) {
      return rejectWithValue(error.message);
    }
  }
);

// Async thunk for verifying OTP
export const verifyOtp = createAsyncThunk(
  'auth/verifyOtp',
  async ({ mobile, idToken, consultantId, apiBase }, { rejectWithValue }) => {
    try {
      const response = await fetch(`${apiBase}/api/bookingconsultancy/verify`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          mobile,
          idToken,
          consultantId,
        }),
      });

      const data = await response.json();
      if (!data.success) {
        return rejectWithValue(data.message || 'Verification failed');
      }
      return data;
    } catch (error) {
      return rejectWithValue(error.message);
    }
  }
);

const authSlice = createSlice({
  name: 'auth',
  initialState: {
    user: null,
    isAuthenticated: false,
    loading: false,
    error: null,
    consultantId: null,
  },
  reducers: {
    login: (state, action) => {
      state.user = action.payload;
      state.isAuthenticated = true;
      state.error = null;
    },
    logout: (state) => {
      state.user = null;
      state.isAuthenticated = false;
      state.consultantId = null;
      state.error = null;
    },
    clearError: (state) => {
      state.error = null;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(registerUser.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(registerUser.fulfilled, (state, action) => {
        state.loading = false;
        state.consultantId = action.payload.consultantId;
      })
      .addCase(registerUser.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      })
      .addCase(verifyOtp.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(verifyOtp.fulfilled, (state, action) => {
        state.loading = false;
        state.isAuthenticated = true;
        state.user = action.payload.user || { mobile: action.meta.arg.mobile };
      })
      .addCase(verifyOtp.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      });
  },
});

export const { login, logout, clearError } = authSlice.actions;
export default authSlice.reducer;